@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap");

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

#notifyContainer {
  position: absolute;
  top: 20rem;
  right: 15rem;
  height: 31rem;
  max-height: 31rem;
  overflow: hidden;
}

body {
  position: relative;
  font-family: "Poppins", sans-serif;
  font-size: 62.5%;
  color: white;
  z-index: -1;
}
